*{
    margin: 0;
    padding: 0;

}
body {
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    background-color: aliceblue;
}
.section {
    height: 100vh;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

p {
    color: grey;
}

.Card {
    height: auto;
    padding: 20px;
    width: 400px;
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: antiquewhite;
    border-radius: 20px;
    text-align: center;
    box-shadow: 0px 2px 20px 10px black;
}

.Details {
    display: flex;
    flex-wrap: wrap;
    width: 300px;
    height: 50px;
    justify-content: space-between  ;
    align-items: center;
    margin: 15px;
    text-align: center;
}
.name {
    text-align: center;
    width: 100px;
}
.section button {
    height: 50px;
    width: 100px;
    border: none;
    background-color: black;
    color: #ffffff;
    border-radius: 20px;
}
